<template>
  <div class="my-gift">
    <div class="gift-content-wrapper">
      <div class="activity-action" @click="gotoAwardRecord">
        <p>奖品兑换记录</p>
      </div>
      <img class="img-responsive" src="../assets/myAwardBg.png" />
      <div class="gift-content">
                <div v-for="(item,index) in prizeList" :key="index">
            <div class="itme-container">
          <div class="left-container">
            <img class="prize-img" :src="getAwardCardImageSrc(item.awardEntity.imageName)">
            <span class="prize-num">x{{item.awardNumber}}</span>
          </div>
          <div class="right-container">
            <p class="prize-name">
              {{item.awardEntity.awardName}}
            </p>
            <p class="prize-value">
              {{item.awardEntity.awardNameAlias}}
            </p>
            <p class="total-des">当前总价值为<span class="total-value">
              {{item.awardNumber*item.awardEntity.awardValue}} </span>元</p>
          </div>
        </div>
        </div>
        <p class="gift-no-tip" v-if="!prizesFlag">{{noPrizesTip}}</p>
      </div>
      <div class="all-prize-value-container" v-if="prizesFlag">
        <p class="all-prize-value" >当前已获奖品总价值：{{getAllValue}}元</p>
      </div>
      <div class="heart-tip">
        <p> 温馨提示：</p>
        <p> 1.为了普惠更多的参与者，本次活动单客户累计兑换的奖品总价值小于5000元。</p>
        <p> 2.客户所兑奖品将于活动结束后由您所在营业部相关工作人员与您联系并核实信息后发放，请您注意保持电话畅通；2020年5月29日之后，对于无法取得联系、未能确认身份等获奖客户，视为弃权，我司有权自行处置相关奖品。如有疑问，可咨询您所在营业部或拨打新时代证券全国统一客服热线95399咨询。 </p>
      </div>
    </div>
    <div class="gift-tip">
      <p>投资有风险 入市需谨慎</p>
    </div>
  </div>
</template>

<script>

import { prizes } from '@/api/prize'
import { myAwards } from '@/api/award'
import { Toast, Indicator } from 'mint-ui';
export default {
  name: 'gift',
  components: {

  },
  data() {
    return {
      prizeList: [],
      awardCardArray: [
        {
          name: 'nqctj',
          srcName: require('../assets/nqctj.png')
        },
        {
          name: 'sysej',
          srcName: require('../assets/nqctj.png')
        },
        {
          name: 'snznj',
          srcName: require('../assets/snznj.png')
        },
        {
          name: 'qjfdj',
          srcName: require('../assets/qjfdj.png')
        }
      ],
      prizesFlag: false,
      noPrizesTip: '暂时没有中奖记录'
    }
  },
  computed: {
    count() {
      return this.$store.state.count;
    },
    getAllValue() {
      let totalPrizeValue = 0;
          for (let i = 0; i < this.prizeList.length; i++) {
              totalPrizeValue += this.prizeList[i].awardNumber*this.prizeList[i].awardEntity.awardValue
            }
        return totalPrizeValue;
    }
  },
  created() {
    let titleObject = {
      back: true,
      romoveMarginTop: false,
      title: "我的奖品"
    }
    this.$emit("routerChange", titleObject);
    this.getMyAwards();
  },
  methods: {
    getFormatDate(requestString, fmt) {
      let requestDate = new Date(requestString);
      console.log(requestDate);
      let o = {
        "M+": requestDate.getMonth() + 1, //月份 
        "d+": requestDate.getDate(), //日 
        "h+": requestDate.getHours(), //小时 
        "m+": requestDate.getMinutes(), //分 
        "s+": requestDate.getSeconds(), //秒 
        "q+": Math.floor((requestDate.getMonth() + 3) / 3), //季度 
        "S": requestDate.getMilliseconds() //毫秒 
      };
      console.log(JSON.stringify(o));
      if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (requestDate.getFullYear() + "").substr(4 - RegExp.$1.length));
      for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
      return fmt;
    },
    getAwardCardImageSrc(name) {
      let src = '';
      for (let index in this.awardCardArray) {
        if (this.awardCardArray[index].name === name) {
          src = this.awardCardArray[index].srcName;
        }
      }
      return src;
    },
    gotoAwardRecord: function () {
      this.$router.push('/awardRecord')
    },
    getMyAwards() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
      });
      myAwards().then((res) => {
        console.log(res);
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          if (res.data.result.length > 0) {
            this.prizeList = res.data.result;
            this.prizesFlag = true;
          }else {
             this.prizesFlag = false;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    }
  }
}
</script>
